@use '../config/_variable.scss' as *;
@use '../config/_mixin.scss' as *;
.about-banner{
    position: relative;
    &__grid{
        display: grid;
        grid-template-columns: repeat(12,1fr); 
        gap: 24px;
    }

    @media only screen and (max-width: 1399.98px) {
        &__title-wrapper{
            grid-column: 1/13;  
        }
        &__YT-wrapper{
            grid-column: 1/13;
            &::before{
                border-radius: 20px;
            }
        }
        &__YT-container{
            border-radius: 20px 20px 0px 0px;
        }

        &__description{
            grid-column: 2/12;
        }
    }

    @media only screen and (max-width: 991.98px) {
        &__YT-wrapper{
            &::before{
                border-radius: 18px;
            }
        }
        &__YT-container{
            border-radius: 18px 18px 0px 0px;
        }
    }

    @media only screen and (max-width: 767.98px) {
        &__grid{
            gap: 0px;
        }
        &__description{
            margin-top: 32px;
            grid-column: 1/13;
            padding: 0px;
        }
        &__YT-container{
            border-radius: 7px 7px 0px 0px;
        }

        &__YT-wrapper{
            &::before{
                border-radius: 7px;
            }
        }
    }
}